import {initializeApp} from "firebase/app";
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import {getAuth, onAuthStateChanged, createUserWithEmailAndPassword, signInWithEmailAndPassword} from "firebase/auth";
import {getFirestore, collection, query, where, onSnapshot, doc, getDoc, getDocs, setDoc, addDoc, updateDoc, deleteDoc} from "firebase/firestore";
import {Loader} from "@googlemaps/js-api-loader";

Function.define (Function.google.fire.base, "app", function (config) { return initializeApp (config); });

Function.define (Function.google.fire.base, "auth", function (app) { return getAuth (app); });
Function.define (Function.google.fire.base.auth, "state", function (auth, context) { return onAuthStateChanged (auth, function (state) { context.call (this, state) }); });
Function.define (Function.google.fire.base.auth, "email", function () {});
Function.define (Function.google.fire.base.auth.email, "register", function (auth, email, password) { return createUserWithEmailAndPassword (auth, email, password); });
Function.define (Function.google.fire.base.auth.email, "login", function (auth, email, password) { return signInWithEmailAndPassword (auth, email, password); });
Function.define (Function.google.fire.base.auth.email, "logout", function (auth) { return signOut (auth); });

Function.define (Function.google.fire, "store", function (app) { return getFirestore (app); });
Function.define (Function.google.fire.store, "collection", function (db, table) { return collection (db, table); });
Function.define (Function.google.fire.store, "snapshot", function (query, context) { return onSnapshot (query, context); });
Function.define (Function.google.fire.store, "doc", function (db, table, id) { return doc (db, table, id); });
Function.define (Function.google.fire.store.doc, "get", function (db, table, id) { return getDoc (db, table, id); });
Function.define (Function.google.fire.store.doc, "set", function (doc, data) { return setDoc (doc, data); });
Function.define (Function.google.fire.store, "select", function (query) { return getDocs (query); });
Function.define (Function.google.fire.store, "insert", function (query, data) { return addDoc (query, data); });
Function.define (Function.google.fire.store, "update", function (doc, data) { return updateDoc (doc, data); });
Function.define (Function.google.fire.store, "delete", function (doc) { return deleteDoc (doc); });

Function.define (Function.google.fire.base, "query", function (collection, where) { return query (collection, where); });
Function.define (Function.google.fire.base.query, "where", function (... condition) { return where (... condition); });

Function.define (Function.map, "loader", function (apiKey) { return new Loader ({apiKey: apiKey}); });
