import vue, {lib, library} from "vue.min.js"
import dom from "vue.min.js/plugin/dom"
let {define} = lib
let {zero, one} = lib

export default {
	"file": function (element, attribute, node) {
		var dom = new lib.dom.doc (element)
		element.onchange = function () {
			vue.model (dom.attribute ("model"), element [("file").concat ("s")][zero])
			}
		},
	"flex-scroll": function (element, attribute, node) {
		var dom = new lib.dom.doc (element)
		var style = {current: dom.attribute ("style") || {}}
		if (("height" in style.current) === false) dom.attribute ("style", lib.object.assign (style.current, {"overflow-y": "scroll", height: dom.client.height + "px !important"}))
		},
	}
