<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	id: "router:link",
	attribute: ["url"],
	setup: function (prop) {
		var url = vue.reference (vue.url.convert (prop.url))
		return {url}
		},
	})
</script>

<template id="router:link">
	<router-link v-if="url" v-bind:to="url"><slot slot/></router-link>
	<a v-else><slot slot/></a>
</template>
