<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	element: "form:submit",
	attribute: ["enter"],
	setup: function (prop) {
		var enter = vue.reference (prop.enter)
		return {vue, enter}
		},
	})
</script>

<template id="form:submit">
	<form v-if="enter" @submit.prevent="enter"><slot slot/><input type="submit" class="none"></form>
	<form v-else><slot slot/></form>
</template>
