<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	element: "icon:material",
	attribute: ["src", "type"],
	setup: function (prop) {
		var src = vue.reference (prop.src)
		var type = vue.reference (style [prop.type] || prop.type || "material-icon")
		return {src, type}
		},
	})
var style = {line: "material-icon-line", round: "material-icon-round", sharp: "material-icon-sharp", tone: "material-icon-tone"}
</script>

<template id="icon:material">
	<span v-if="src" v-bind:class="type" element="icon:material" icon>{{ src }}</span>
	<span v-bind:class="type" element="icon:material" icon v-else><slot slot/></span>
</template>
